import React from 'react';
import s from "./Home.module.scss"
import Carousel from "../../CarouselContainer/CarouselContainer";
import bgImg from "../../../assets/rev-bg.jpg";

const Home = () => {
    return (
      <div className={s.Home}>
        <div
          className={s.HomeCarousel}
          style={{
            backgroundImage: `url(${bgImg})`
          }}
        >
          <Carousel/>
        </div>
        <div className={s.content}>
            <div className={s.catTitle}>Категории товаров:</div><br/>
            <div className={s.catBody}>
                <div className={s.block}>

                    <div className={s.blockBody}
                         style={{
                             backgroundImage: `url(${bgImg})`
                         }}
                    ></div>
                    <div className={s.blockTitle}

                    >Изображения</div>
                </div>
                <div className={s.block}>
                    <div className={s.blockBody}
                         style={{
                             backgroundImage: `url(${bgImg})`
                         }}
                    ></div>
                    <div className={s.blockTitle}>Сумки</div>

                </div>
                <div className={s.block}>

                    <div className={s.blockBody}

                         style={{
                             backgroundImage: `url(${bgImg})`
                         }}></div>
                    <div className={s.blockTitle}>Скатерти</div>

                </div>
            </div>


        </div>
      </div>
    );
}

export default Home;
